* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
    font-family: 'Roboto', sans-serif;
    scroll-behavior: smooth;
}

ul {
    list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
    font-weight: 400;
    font-size: 1rem;
}

a {
    text-decoration: none;
}
